$animation-delay :2;
@mixin transform($x,$y)
{
  -webkit-transform: translateX($x) rotate($y);
      -moz-transform: translateX($x) rotate($y);
      -ms-transform: translateX($x) rotate($y);
      -o-transform: translateX($x) rotate($y);
      transform: translateX($x) rotate($y);
}


body{
  box-sizing: border-box;
  margin: 0;
}

.box{
position: absolute;
// background: rgba(76,76,76,1);
// background: -moz-radial-gradient(center, ellipse cover, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 0%, rgba(44,44,44,1) 72%, rgba(71,71,71,1) 90%, rgba(28,28,28,1) 98%, rgba(102,102,102,1) 100%);
// background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(76,76,76,1)), color-stop(0%, rgba(89,89,89,1)), color-stop(72%, rgba(44,44,44,1)), color-stop(90%, rgba(71,71,71,1)), color-stop(98%, rgba(28,28,28,1)), color-stop(100%, rgba(102,102,102,1)));
// background: -webkit-radial-gradient(center, ellipse cover, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 0%, rgba(44,44,44,1) 72%, rgba(71,71,71,1) 90%, rgba(28,28,28,1) 98%, rgba(102,102,102,1) 100%);
// background: -o-radial-gradient(center, ellipse cover, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 0%, rgba(44,44,44,1) 72%, rgba(71,71,71,1) 90%, rgba(28,28,28,1) 98%, rgba(102,102,102,1) 100%);
// background: -ms-radial-gradient(center, ellipse cover, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 0%, rgba(44,44,44,1) 72%, rgba(71,71,71,1) 90%, rgba(28,28,28,1) 98%, rgba(102,102,102,1) 100%);
// background: radial-gradient(ellipse at center, rgba(76,76,76,1) 0%, rgba(89,89,89,1) 0%, rgba(44,44,44,1) 72%, rgba(71,71,71,1) 90%, rgba(28,28,28,1) 98%, rgba(102,102,102,1) 100%);
// filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4c4c4c', endColorstr='#666666', GradientType=1 );
overflow: hidden;
width: 100%;
height: 100%;
left:0;
top:0;
}

img{
  width:100px;
  height:100px;
}

.box div{
  position: absolute;
  display: block;
  animation-name:  motion;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-duration: 15s;
  z-index: 100;
}

.box div:nth-child(1){
  margin-left:0%;
  animation-delay:  $animation-delay*-3s;

}
.box div:nth-child(2){
  margin-left:13%;
  animation-duration: 20s;
  animation-delay: $animation-delay*-2s;
}
.box div:nth-child(3){

  margin-left:24%;
  animation-duration: 18s;
  animation-delay: $animation-delay*-5s;
}
.box div:nth-child(4){


  margin-left:40%;
  animation-duration: 13s;
  animation-delay: $animation-delay*-1s;
}
.box div:nth-child(5){

  margin-left:58%;
  animation-duration: 21s;
  animation-delay: $animation-delay*-6s;
}
.box div:nth-child(6){

  margin-left:70%;
  animation-duration: 17s;
  animation-delay: $animation-delay*-4s;
}
.box div:nth-child(7){

  margin-left:82%;
  animation-duration: 23s;
  animation-delay: $animation-delay*-1s;
}
.box div:nth-child(8){

  margin-left:95%;
  animation-duration: 15s;
  animation-delay: $animation-delay*-1s;
}


@keyframes motion {

  0%{
    top:-10%;
    opacity: 0;
    @include transform(-25px,0deg);

  }
  20%{
    opacity:1;
    @include transform(25px,45deg);
  }
  40%{
    @include transform(-25px,90deg);
  }
  60%{
    @include transform(25px,135deg);
  }
  80%{
      @include transform(-25px,180deg);
  }
  100%{
    top:110%;
    @include transform(25px,225deg);
  }
}

.box2 div img{
  transform: scale(1.4) rotateY(180deg);
  filter: blur(2px);
}
.box2 div{
  animation-direction: reverse;
  z-index: 200;
}

.box3 div img{
  transform: scale(0.8);
  filter: blur(4px);
}
.box3 div{
  animation-duration: 30s !important;
  transform-origin: top;
  z-index: -10;
  padding-left: 10%;
}



h1{
  color: black;
  position: absolute;
  top: 30%;
  font-size:8em;
  width: 100%;
  text-align: center;
  z-index: 0;
}
